import Image from "next/image";
import home_page_1 from "@/public/images/home_page_1.svg";
import home_page_2 from "@/public/images/home_page_2.svg";
import home_page_3 from "@/public/images/home_page_3.svg";
import home_page_4 from "@/public/images/home_page_4.svg";
import home_page_5 from "@/public/images/home_page_5.svg";

const Home = () => {
  return (
    <div className="relative overflow-hidden min-h-screen bg-black">
      {/* الخلفية */}
      <Image
        src={home_page_1}
        alt="background"
        layout="fill"
        className="object-cover z-0"
      />

      {/* الصورة الأولى */}
      {/* <Image
        src={home_page_2}
        alt="foreground"
        width={300}
        height={200}
        className="absolute top-24 left-20 z-10"
      /> */}
      
      {/* الصور الأخرى */}
      <Image
        src={home_page_3}
        alt="additional"
        width={250}
        height={150}
        className="absolute bottom-10 left-10 z-20"
      />
      <Image
        src={home_page_4}
        alt="additional"
        width={250}
        height={150}
        className="absolute bottom-20 right-20 z-20"
      />
      <Image
        src={home_page_5}
        alt="additional"
        width={250}
        height={150}
        className="absolute top-10 right-10 z-20"
      />
      
      {/* المحتوى النصي */}
      <div className="absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 z-30 text-center">
        <h1 className="text-yellow-400 font-bold text-5xl">مع فريق إييا با ميديا</h1>
        <p className="text-white mt-4 text-lg">إبداع، احترافية، حلول مبتكرة</p>
        <button className="mt-4 py-2 px-6 bg-blue-500 text-white rounded-lg">آراء وتقيمات عملائنا</button>
      </div>
    </div>
  );
};

export default Home;
